* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


.navbar-main {
    display: flex;
    flex-direction: row;
    max-width: 100%;
    gap: 2%;
    position: sticky;
    width: 100%;
    background: white;
    z-index: 1000;

}

.ref {
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Inter;
    font-size: 16px;
    font-weight: 500;
    line-height: 19px;
    gap: 2rem;



}

.call {

    display: flex;

    align-items: center;
    font-family: Inter;
    font-size: 17px;
    font-weight: 600;
    line-height: 21px;
    letter-spacing: 0px;
    gap: 10px;
}

.logo {
    background: #3056D3;
    display: flex;
    height: 6.25rem;
    flex-basis: 25%;
    align-items: center;
    justify-content: flex-end;
}



.call svg {
