}

.links {
    text-decoration: none;
    color: #151828;

    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
}

.title {
    color: #FFFFFF;
}

.logo div {
    margin-right: 45px;
    display: flex;
    align-items: center;

}

.call div {
    display: flex;
    flex-direction: column;

}

.call_us {
    font-family: Inter;
    font-size: 14px;
    font-weight: 500;
    line-height: 17px;
    letter-spacing: 0px;
    color: #637381;

}

.img3 {
    height: auto;
    width: 100%;
    height: 100%;
}

.number {
    text-decoration: none;
    font-family: Inter;
    font-size: 17px;
    font-weight: 600;
    line-height: 21px;
    letter-spacing: 0px;
    color: #212B36;

}

.banner {
    height: calc(100vh - 6.25rem);



    display: flex;
    align-items: center;

}

.box {
    block-size: auto;
    width: 570px;
    height: 410px;
    background-color: #FFFFFF;
    position: absolute;
    margin-left: 5%;
}

.box p {
    height: 19px;
