}

.submit1 input {
    background: #3056D3;
    font-family: Inter;
    font-size: 15px;
    font-weight: 500;
    width: 150px;
    height: 45px;
    color: #FFFFFF;
    cursor: pointer;
    border-radius: 5px;
}

.a1 {
    color: #212B36;
    font-size: 35px;
    font-family: Inter;
    font-weight: 700;
    line-height: 45.5px;

}

.link--arrowed {
    display: inline-block;
    height: 2rem;
    line-height: 2rem;
}

.link--arrowed .arrow-icon {
    position: relative;
    top: -1px;
    transition: transform 0.3s ease;
    vertical-align: middle;
}

.link--arrowed .arrow-icon--circle {
    transition: stroke-dashoffset 0.3s ease;
    stroke-dasharray: 95;
    stroke-dashoffset: 95;
}

.link--arrowed:hover .arrow-icon {
    transform: translate3d(5px, 0, 0);
}

.link--arrowed:hover .arrow-icon--circle {
    stroke-dashoffset: 0;
}

.centered-container {
    display: inline-flex;

}

.link {
    color: #2175FF;
    cursor: pointer;
    font-weight: 400;
    text-decoration: none;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    margin-left: 25px;
    color: #212B36;
}

.about {
