}

@media (max-width:920px) {
    .main8 {

        text-align: center;
    }

    .tp .row div {
        display: flex;
        /* align-items: center; */
        justify-content: center;
        margin-top: 5%;
    }

    .navbar-main {
        flex-direction: column;
    }

    .ref {
        flex-direction: column;
    }

    .hamburger1 {
        display: block;
        position: absolute;
        right: 0;

    }

    .hamburger1 div {
        background-color: rgb(255, 255, 255);
        position: relative;
        width: 40px;
        height: 5px;
        margin-top: 7px;
        margin-right: 7px;
        -webkit-transition: all 0.2s ease-in-out;
        transition: all 0.2s ease-in-out;
    }

    #toggle1 {
        display: none;
    }

    #toggle1:checked+.hamburger1 .top {
        -webkit-transform: rotate(-45deg);
        transform: rotate(-45deg);
        margin-top: 22.5px;
    }

    #toggle1:checked+.hamburger1 .meat {
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
        margin-top: -5px;
    }

    #toggle1:checked+.hamburger1 .bottom {
        -webkit-transform: scale(0);
        transform: scale(0);
    }

    #toggle1:checked~.menu1 {

        height: 340px;
        width: 100%;
    }


    /* Menu */
    .menu1 {
        width: 100%;
        background-color: #ffffff;
        margin: 0;
        display: -ms-grid;
        display: grid;
        grid-template-rows: 1fr repeat(4, 0.5fr);
        grid-row-gap: 25px;
        padding: 0;
        list-style: none;
        clear: both;
        width: auto;
        text-align: center;
        height: 0px;
        overflow: hidden;
        transition: height .4s ease;
        z-index: 120;
        -webkit-transition: all 0.3s ease;
        transition: all 0.3s ease;
    }

    .menu1 a:first-child {
        margin-top: 40px;
    }

    .menu1 a:last-child {
        margin-bottom: 40px;
    }

    .link1 {
        width: 100%;
        margin: 0;
        padding: 10px 0;
        font: 700 20px 'Oswald', sans-serif;
    }

    .link1:hover {
        background-color: #fff;
        color: rgb(61, 61, 61);
        -webkit-transition: all 0.3s ease;
        transition: all 0.3s ease;
    }


    .tp {
        display: flex;
        justify-content: center;
    }

    .row {
        display: block !important;
        margin: 0 !important;
    }

    .p12 {
        height: auto;
        width: auto;
    }

    .img_grid .row div {
        margin-top: 10%;

    }

    .img_grid {
        gap: 5%;
    }

}

